import React, { useState } from 'react';
import { Button, Grid } from '@material-ui/core';
import _isEmpty from 'lodash.isempty';
import _get from 'lodash.get';
import { makeStyles } from '@material-ui/core/styles';
import SearchField from '../SearchField';
import StatusSelector from '../StatusSelector';
import { TSearchFilterForm } from '../../../../types';

type Props = {
	onSearch: (values: TSearchFilterForm) => void;
	onTriggerSearch: (b: boolean) => void;
	statuses: Array<Object>;
};

const useStyles = makeStyles(() => ({
	form: {
		width: '100%',
	},
}));

const SearchFilter = ({ onSearch, onTriggerSearch, statuses }: Props) => {
	const classes = useStyles();
	const [search, setSearch] = useState('');
	const [selectedStatuses, setSelectedStatuses] = useState([]);

	const onSubmit = (e: React.SyntheticEvent) => {
		e.preventDefault();
		const validStatues = selectedStatuses.map(status => _get(status, 'Id'));
		const searchText = !_isEmpty(validStatues) && !search ? '*' : search;
		if (searchText)
			onSearch({
				search: searchText,
				statuses: validStatues,
			});
		onTriggerSearch(Boolean(searchText));
	};

	return (
		<form onSubmit={onSubmit} className={classes.form}>
			<Grid
				container
				direction='row'
				justifyContent='flex-start'
				alignItems='center'
				spacing={1}
			>
				<Grid item xs={12} lg={4}>
					<SearchField
						search={search}
						setSearch={setSearch}
						handleReset={() => {
							setSearch('');
						}}
						onTriggerSearch={onTriggerSearch}
					/>
				</Grid>
				{!_isEmpty(statuses) && (
					<Grid item xs={12} lg={7}>
						<StatusSelector
							values={selectedStatuses}
							setValues={setSelectedStatuses}
							statuses={statuses}
						/>
					</Grid>
				)}
				<Grid item xs={12} lg={1}>
					<Button type='submit' variant='contained' color='primary' size='large'>
						Search
					</Button>
				</Grid>
			</Grid>
		</form>
	);
};

export default SearchFilter;
